*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

body {
  overflow: hidden;
  background-color: #7945ff;
  background-image: url(./../assets/Desktop_Bg.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 48rem) {
  body {
    background-image: url(./../assets/Mobile_Bg.png);
  }
}

body.scroll {
  overflow: unset;
  overflow-x: hidden;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

button, input {
  background: transparent;
  border: none;
  outline: none;
}

#root {
  height: 100vh;
  width: 100vw;
}

.center-f {
  display: flex;
  align-items: center;
}

.center-g, .loader, .intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button, .intro {
  display: grid;
  place-items: center;
}

.position-flow {
  position: absolute;
  top: 0;
  left: 0;
}

.smoothPointer, .intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button {
  transition: 0.5s;
  cursor: pointer;
}

.container {
  padding: 0 15px;
  margin: 0 auto;
}

/* ------------------------- Start style Container ------------------------- */
.container {
  padding: 0 15px;
  margin: 0 auto;
}

/* Small */
@media (min-width: 48rem) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 62rem) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 75rem) {
  .container {
    width: 1170px;
  }
}
.intro {
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.intro .intro-wrapper {
  text-align: center;
}
.intro .intro-wrapper .hideUp {
  transform: translateY(-1.875rem);
  opacity: 0;
  transition: 0.5s;
}
.intro .intro-wrapper .show {
  transform: translateY(0);
  opacity: 1;
  transition: 0.5s;
}
.intro .intro-wrapper img {
  height: 4.375rem;
  margin-bottom: 4rem;
}
.intro .intro-wrapper .buttons {
  display: flex;
  flex-direction: column;
}
.intro .intro-wrapper .buttons button {
  height: 4.063rem;
  width: 25rem;
  border: 0.125rem solid #000;
  border-radius: 0.938rem;
  box-shadow: 0 0.438rem 0 #000;
  margin: 0.45rem 0;
  font-size: 1.25rem;
  font-weight: 800;
  text-transform: uppercase;
  text-align: start;
  padding: 0 1.25rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.intro .intro-wrapper .buttons button:hover {
  filter: brightness(1.1);
}
.intro .intro-wrapper .buttons button:active {
  transform: translateY(0.375rem);
  box-shadow: unset;
}
.intro .intro-wrapper .buttons button:nth-child(1) {
  background-color: #fa6787;
  color: #fff;
}
.intro .intro-wrapper .buttons button:nth-child(2) {
  background-color: #ffcd6a;
}
.intro .intro-wrapper .buttons button:nth-child(3) {
  background-color: #fff;
}
.intro .intro-wrapper .gameRules {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 37.5rem;
  transform: translate(-50%, -50%);
  display: none;
}
@media (max-width: 48rem) {
  .intro .intro-wrapper .gameRules {
    width: 95%;
  }
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper {
  background-color: #fff;
  padding: 2rem 1.65rem 3rem;
  border-radius: 1.563rem;
  border: 0.188rem solid #000;
  box-shadow: 0 0.438rem 0 #000;
  transition: 0.5s;
  transform: scale(0);
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper h1 {
  font-size: 2.625rem;
  text-transform: uppercase;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .objective,
.intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay {
  text-align: start;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .objective h4,
.intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay h4 {
  color: #5529c5;
  font-size: 1.125rem;
  text-transform: uppercase;
  margin-bottom: 0.65rem;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .objective p,
.intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay p {
  font-weight: 500;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .objective {
  margin: 1.25rem 0 1rem;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .objective p {
  line-height: 1.563rem;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay .line {
  display: flex;
  margin: 1rem 0;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay .line span {
  font-weight: 800;
  margin-right: 1rem;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper {
  position: absolute;
  bottom: -1.875rem;
  left: 50%;
  transform: translateX(-50%);
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button {
  height: 3.75rem;
  width: 3.75rem;
  background-color: #fa6787;
  border: 0.188rem solid #000;
  border-radius: 50%;
  box-shadow: 0 0.313rem 0 #000;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button:active {
  transform: scale(0.93);
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button::after, .intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button::before {
  content: "";
  height: 0.188rem;
  width: 2.188rem;
  background-color: #000;
  position: absolute;
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button::after {
  transform: rotate(45deg);
}
.intro .intro-wrapper .gameRules .gameRules_Wrapper .button_wrapper button::before {
  transform: rotate(-45deg);
}
@media (max-width: 37.5rem) {
  .intro .intro-wrapper .gameRules .gameRules_Wrapper {
    padding: 1.625rem 1.375rem 2.25rem;
  }
  .intro .intro-wrapper .gameRules .gameRules_Wrapper h1 {
    font-size: 2rem;
  }
  .intro .intro-wrapper .gameRules .gameRules_Wrapper p {
    font-size: 0.875rem;
  }
  .intro .intro-wrapper .gameRules .gameRules_Wrapper .objective h4,
  .intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay h4 {
    font-size: 1rem;
  }
  .intro .intro-wrapper .gameRules .gameRules_Wrapper .objective .line span,
  .intro .intro-wrapper .gameRules .gameRules_Wrapper .howToPlay .line span {
    margin-right: 0.625rem;
  }
}
@media (max-width: 37.5rem) {
  .intro .intro-wrapper img {
    height: 3.125rem;
  }
  .intro .intro-wrapper .buttons button {
    height: 3.438rem;
    width: 18.75rem;
    font-size: 1rem;
    box-shadow: 0 0.313rem 0 #000;
    padding: 0 1rem;
  }
  .intro .intro-wrapper .buttons button .PlayerRobotSVG,
  .intro .intro-wrapper .buttons button .TwoFaces {
    transform: scale(0.9);
  }
}

.PlayerRobotSVG {
  display: flex;
}
.PlayerRobotSVG .playerFace,
.PlayerRobotSVG .robotFace {
  height: 2.5rem;
  width: 2.5rem;
  border: 0.125rem solid #000;
  border-radius: 50%;
  position: relative;
}
.PlayerRobotSVG .playerFace::after, .PlayerRobotSVG .playerFace::before,
.PlayerRobotSVG .robotFace::after,
.PlayerRobotSVG .robotFace::before {
  content: "";
  position: absolute;
  top: 0.625rem;
  height: 0.375rem;
  width: 0.125rem;
  background-color: #000;
}
.PlayerRobotSVG .playerFace::after,
.PlayerRobotSVG .robotFace::after {
  left: 0.75rem;
}
.PlayerRobotSVG .playerFace::before,
.PlayerRobotSVG .robotFace::before {
  right: 0.75rem;
}
.PlayerRobotSVG .playerFace {
  transform: translateX(0.5rem);
  background-color: #fa6787;
  z-index: 9;
}
.PlayerRobotSVG .playerFace span {
  position: absolute;
  height: 1.688rem;
  width: 1.688rem;
  border: 0.125rem solid;
  border-radius: 50%;
  border-color: transparent transparent #000 #000;
  transform: rotate(315deg) translate(0.063rem, 0.375rem);
}
.PlayerRobotSVG .robotFace span {
  position: absolute;
  height: 0.125rem;
  width: 1.375rem;
  transform: translate(0.438rem, 1.125rem);
  background-color: #000;
}

.TwoFaces {
  display: flex;
}
.TwoFaces .happyFace:first-child {
  transform: translateX(0.5rem);
  background-color: #ffcd6a;
  z-index: 9;
}
.TwoFaces .happyFace {
  height: 2.5rem;
  width: 2.5rem;
  border: 0.125rem solid #000;
  border-radius: 50%;
  position: relative;
}
.TwoFaces .happyFace::after, .TwoFaces .happyFace::before {
  content: "";
  position: absolute;
  top: 0.625rem;
  height: 0.375rem;
  width: 0.125rem;
  background-color: #000;
}
.TwoFaces .happyFace::after {
  left: 1.063rem;
}
.TwoFaces .happyFace::before {
  right: 0.5rem;
}
.TwoFaces .happyFace span {
  position: absolute;
  height: 1.688rem;
  width: 1.688rem;
  border: 0.125rem solid;
  border-radius: 50%;
  border-color: transparent transparent transparent #000;
  transform: rotate(270deg) translate(0.063rem, 0.563rem);
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #7945ff;
  z-index: 99999;
}
@media (max-width: 37.5rem) {
  .loader > span {
    transform: scale(0.8);
  }
